.inputWidget {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 58px;
}
.inputWidget .input {
  box-sizing: border-box;
  color: #040404;
  font-weight: 600;
  width: 100%;
  height: 100%;
  border: 1px solid #a8a8a8;
  text-align: left;
  border-radius: 10px;
  padding: 0.75em 1.13em;
}
.inputWidget .input:disabled {
  background: #f6f7fb;
  border: 1px solid #f6f7fb;
}
.inputWidget .input.error, .inputWidget .input.error:disabled {
  border-color: #ed1b22;
}
.inputWidget .input:focus {
  color: #040404;
}
.inputWidget .input:focus ~ .label {
  top: 10%;
  font-size: 0.75em;
  color: #707070;
}
.inputWidget .currency {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-right: 0.5em;
}
.inputWidget .label {
  color: #707070;
  top: 30%;
  left: 0;
  position: absolute;
  transition: all 0.2s;
  pointer-events: none;
  font-size: 1em;
  margin-left: 1.67em;
  font-size: 1em;
}
.inputWidget .label.notEmpty {
  top: 10%;
  font-size: 0.75em;
  color: #707070;
  /* @include respond(xs) {
    margin-left: 1.67em;
  } */
}
.inputWidget.soft.placeholder {
  padding-top: 0.85em;
}
.inputWidget.soft .input {
  border-radius: 0;
  border: none;
  border-bottom: 2px solid #e1e1e1;
  padding: 0.75em 0;
}
.inputWidget.soft .input:focus {
  color: #373742;
}
.inputWidget.soft .input:focus ~ .label {
  top: 0;
  color: #65a1ee;
}
.inputWidget.soft .input:focus ~ .hr {
  transform: scaleX(1);
}
.inputWidget.soft .label {
  color: #373742;
  top: calc(50% - 0.2em);
  margin-left: 0;
}
.inputWidget.soft .label.notEmpty {
  top: 0;
  color: #65a1ee;
}
.inputWidget.soft .hr {
  position: absolute;
  bottom: 0;
  transition: transform 0.2s;
  transform: scaleX(0);
  width: 100%;
  border: none;
  border-bottom: 2px solid #65a1ee;
  display: block;
}
.inputWidget.soft .error {
  color: #ed1b22;
}